@import '../untils/variables';

.form-radio {
  margin-right: 10px;
  display: flex;
  align-items: center;
  color: $form-radio-color;
  font-size: 17px;
  font-weight: bold;
  line-height: 18px;
  user-select: none;
  cursor: pointer;


  &__control {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked {
      $radioIsChecked: & !global;
    }
  }

  &__text {
    display: inline-flex;
    align-items: center;
    font-size: 17px;
    line-height: 18px;
    user-select: none;
    cursor: pointer;

    &:hover {
      &::before {
        border-color: $border-radio-hover;
      }
    }

    &:active {
      &::before {
        border-color: $border-radio-active;
      }
    }


    &::before {
      content: "";
      width: 21px;
      height: 21px;
      flex-shrink: 0;
      flex-grow: 0;
      margin-right: 5px;
      border: 2px solid $border-radio;
      border-radius: 50%;
      box-sizing: border-box;


      #{$radioIsChecked} + & {
        background: url("data:image/svg+xml,%3Csvg width='21' height='21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.24.393c5.424 0 9.821 4.397 9.821 9.822 0 5.424-4.397 9.822-9.822 9.822-5.424 0-9.821-4.398-9.821-9.822C.418 4.79 4.815.393 10.24.393z' fill='%23444444'/%3E%3C/svg%3E") 50% 50% no-repeat;
        background-size: 9px 9px;

      }
    }
  }
}

