@import '../untils/variables';

.table {
  margin: 0 20px;
  font-size: 14px;
  line-height: 24px;
  border-collapse: collapse;
  text-align: center;
  overflow:hidden;

  &__row {
    width: 100vh;
  }

  &__cell {
    padding: 5px;
    text-align: center;
    width: 66px;
    height: 20px;
    border: 1px solid $table-border-color;
  }
}




