@import '../untils/variables';

.recording {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 80px 1fr;
  min-height: 100vh;
  background: $recording-bg;

  &--loading {
    $recording--loading: & !global;
  }

  &__lid{
    grid-column: 1/2;
    grid-row: 1/2;
    display: flex;
    align-items: center;
    margin: 10px 20px;
    padding:10px;
    border-radius: 6px;
    border: 1px $recording-lid-border-color solid;
    background: $recording-lid-background-color;

    #{$recording--loading} & {
      visibility: hidden;
    }
  }

  &__date {
    grid-column: 2/3;
    grid-row: 1/2;
    display: flex;
    justify-content: center;
    padding: 10px;

    #{$recording--loading} & {
      visibility: hidden;
    }
  }

  &__pre {
    grid-column: 3/4;
    grid-row: 1/2;
    display: flex;
    align-items: center;
    margin: 10px 20px;
    padding:10px;
    border-radius: 6px;
    border: 1px $recording-pre-border-color solid;
    background: $recording-pre-background-color;

    #{$recording--loading} & {
      visibility: hidden;
    }
  }

  &__table {
    grid-column: 1/4;
    grid-row: 2/3;

    #{$recording--loading} & {
      visibility: hidden;
    }
  }

  &__text {
    padding: 10px 15px;
    font-size: 17px;
    font-weight: 700;
    line-height: 18px;
  }

  &__loader {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;
    #{$recording--loading} & {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    span {
      width: 30px;
      height: 30px;
      border-radius: 100%;
      background-color: $recording-loader-color;
      margin: 35px 5px;
      opacity: 0;

      &:nth-child(1) {
        animation: opacityChange 1s ease-in-out infinite;
      }

      &:nth-child(2) {
        animation: opacityChange 1s ease-in-out 0.33s infinite;
      }

      &:nth-child(3) {
        animation: opacityChange 1s ease-in-out 0.66s infinite;
      }
    }
  }
}

@keyframes opacityChange {
  0%,
  100% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
}
